<template>
  <div class='comment-list'>
    <div class="commet-content">
      <div class="comment-item flex-row border-1px" v-for="(item, index) in list" :key="index">
        <div class="left">
          <div class="avatar iconSize">
            <img v-if="item.avatar" :src="item.avatar" alt="">
            <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png" v-else alt="">
          </div>
        </div>
        <div class="right">
          <div class="userInfo flex-row">
            <div class="nickname" :class="{is_self: item.is_self}">{{$utils.splitStr(item.nickname, 11)}}</div>
            <div class="grade">
              <div class="grade-icon">
                <div class="grade_progress" :style="{width: `${item.score * 10}%`}"></div>
              </div>
            </div>
            <div class="digg_info flex-row"  @click="setFabulous(index)" :class="{active: item.is_fabulous === 1}">
              <div class="digg-icon iconSize">
                <img src="../../../assets/img/details/digg_active.png" v-if="item.is_fabulous === 1" alt="">
                <img src="../../../assets/img/details/digg.png" v-else alt="">
              </div>
              <div class="text">{{item.fabulous ? item.fabulous : 0 | setPeople}}</div>
            </div>
          </div>
          <div class="content">{{item.content}}</div>
          <div class="others">
            <div class="update_time">{{$utils.timeFormat(item.comment_time * 1000)}}</div>
            <!-- <div class="edit" @click="filmEdit(index)" v-if='item.is_self === 1'></div>
            <div class="delete" @click="filmDelete(index)" v-if='item.is_self === 1'></div> -->
          </div>
        </div>
        <div class="digg-content">

        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  methods: {
    filmEdit (index) {
      this.$emit('filmOperate', {
        index,
        operate: 1
      })
    },
    filmDelete (index) {
      this.$emit('filmOperate', {
        index,
        operate: 2
      })
    },
    setFabulous (index) {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      if (this.list[index].is_fabulous !== 1) {
        this.$emit('filmOperate', {
          index,
          operate: 3
        })
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.comment-list {
  .comment-item {
    padding: 40px 20px 30px;
    position: relative;
    &:last-child {
      &::before {
        display: none;
      }
    }
    &::before {
      left: 20px;
      right: 20px;
      width: auto;
    }
    .left {
      height: 100%;
      align-self: flex-start;
      .avatar {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 20px;
      }
    }
    .right {
      flex:1;
      .userInfo {
        align-items: flex-end;
        margin-bottom: 20px;
        .nickname {
          text-align: left;
          margin-right: 20px;
          color: #666;
          &.is_self {
            color: #FF4112;
          }
        }
        .grade {
          height: 30px;
          margin-bottom: -2px;
          flex: 1;
        }
        .digg_info {
          display: flex;
          align-items: flex-end;
          .digg-icon {
            width: 39px;
            height: 42px;
            margin-right: 8px;
          }
          .text {
            line-height: 34px;
          }
          &.active {
            .text {
              color: #ff4b1f
            }
          }
        }
      }
      .content {
        line-height: 42px;
        max-height: 126px;
        text-align: justify;
        font-size: 30px;
        overflow: hidden;
        text-overflow: -o-ellipsis-lastline;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        line-clamp: 3;
        /* autoprefixer: off */
        -webkit-box-orient: vertical;
        /* autoprefixer: on */
      }
      .others {
        text-align: left;
        display: flex;
        justify-content: flex-start;
        margin-top: 20px;
        align-items: center;
        .update_time {
          color: #666
        }
        .edit, .delete {
          border: 1px solid #F4A82B;
          border-radius: 5px;
          width: 74px;
          height: 38px;
          line-height: 38px;
          text-align: center;
          color: #F4A82B;
          margin-left: 20px;
        }
        .delete {
          color: #ff4b1f;
          border-color: #ff4b1f;
        }
      }
    }
  }
}
</style>



