<template>
  <div id="recommend">
    <swiper class="swiper type2" :options='swiperOptions' ref="swiperRecommend" v-if="list.length > 0">
      <swiper-slide class="slide list-item" v-for="(item, index) in list" @click.native="toDetail(index)" :key='index'>
        <div class="item-cover">
          <img :src="$utils.getPlayUrl(item.vod_pic)" onerror="this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/default_image.png'" alt="">
        </div>
        <div class="title">{{item.vod_name}}</div>
        <div class="tip">{{item.vod_class}}</div>
        <div class="tip">{{item.vod_hits ? item.vod_hits : 0 | setFloatNumber}}</div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    list: {
      type: Array
    },
    isRefresh: {
      type: Number
    }
  },
  computed: {
    swiperRecommend () {
      return this.$refs.swiperRecommend.swiper
    }
  },
  data () {
    return {
      swiperOptions: {
        slidesPerView: 'auto',
        spaceBetween: 0
      }
    }
  },
  methods: {
    toDetail (index) {
      this.$router.push(`/film/detail/${this.list[index].vod_id}`)
    }
  },
  watch: {
    isRefresh () {
      this.$nextTick(() => {
        this.swiperRecommend.slideTo(0, 0)
      })
    }
  }
}
</script>
<style lang="scss" scoped>
#recommend {
  padding-left: 20px;
  .type2 {
    .list-item {
      width: 230px;
      padding-bottom: 30px;
      margin: 0 10px;
      .title {
        text-align: center;
        color: #333;
        padding: 0 20px;
      }
      .tip {
        text-align: center;
      }
      .item-cover {
        height: 322.4px;
        position: relative;
        overflow: hidden;
        background: #dadada;
        img {
          position: absolute;
          left: 50%;
          width: 100%;
          top: 50%;
          transform: translate(-50%, -50%);
        }
      }
      .name {
        display: flex;
        padding-right: 20px;
        line-height: 40px;
        font-size: 28px;
        .text {
          flex: 1;
        }
        .grade-count {
          margin-left: 10px;
          color: #F4A82B;
        }
      }
      .updateTime {
        // height: 34px;
        line-height: 40px;
        position: absolute;
        font-size: 28px;
        box-sizing: border-box;
        bottom: 0;
        width: 100%;
        color: #fff;
        padding: 0 10px;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        background-image: linear-gradient(-180deg, rgba(194,190,190,0.20) 0%, rgba(0,0,0,0.62) 100%);
      }
      .title {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 32px;
        line-height: 45px;
        font-weight: 900;
        padding: 16px 0px 5px 0;
      }
      .tip {
        color: #666;
        font-size: 28px;
        line-height: 40px;
      }
    }
  }
}
</style>



