<template>
  <div class='film-comment'>
    <div class="title"></div>
    <div class="comment-area">
      <div class="top-logo">
        <div class="juejinbao">
          <div class="logo iconSize">
            <img src="../../../assets/img/details/logo.png" alt="logo">
          </div>
          <div class="text"><span>{{details.review_score * 1 ? details.review_score + '' : ''}}</span></div>
        </div>
        <div class="douban juejinbao">
          <div class="logo iconSize">
            <img src="../../../assets/img/details/dou.png" alt="logo">
          </div>
          <div class="text">: <span class="active">{{details.vod_douban_score ? details.vod_douban_score : 0}}</span></div>
        </div>
      </div>
      <div class="" :class="{grayBg: details.is_review === 1}">
        <div v-if="details.is_review === 1" class='currentUser comment-item flex-row'>
          <div class="left">
            <div class="avatar iconSize">
              <img :src="comment.avatar" onerror="javascript:this.src=https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png" alt="">
            </div>
          </div>
          <div class="right">
            <div class="userInfo flex-row">
              <div class="nickname" :class="{is_self: comment.is_self}">{{$utils.splitStr(comment.nickname, 11)}}</div>
              <div class="grade">
                <div class="grade-icon">
                  <div class="grade_progress" :style="{width: `${comment.score * 10}%`}"></div>
                </div>
              </div>
              <!-- <div class="digg_info flex-row" :class="{active: comment.is_fabulous === 1}">
                <div class="edit" @click="setEdit"></div>
              </div> -->
            </div>
            <div class="others">
              <div class="update_time">{{$utils.timeFormat(comment.comment_time * 1000)}}</div>
            </div>
            <div class="content">{{comment.content}}</div>
          </div>
        </div>
        <div class="flex-row edit-content jusB" v-if="details.is_review === 1">
          <div class="edit" @click="setEdit"></div>
          <div class="delete" @click="isDelete=true"></div>
          <div class="button flex-row generate"  @click="toImage"></div>
        </div>
        <div class="button no_review flex-row" v-else @click="editComment"></div>
      </div>
    </div>
    <comment-list v-if='reviewList && reviewList.length > 0' @filmOperate="filmOperate" :list="reviewList"></comment-list>
    <div class="check-all border-1px" v-if="reviewList && reviewList.length > 5" @click="checkAll"></div>
    <!--  -->
    <div class="commentEdit" style="z-index: 3" v-if="showEdit">
      <div class="commentEditBox">
        <div class="edit-title">{{details.vod_name}}</div>
        <div class="tip" v-if="comment.score * 1 === 0"></div>
        <div class="gradeCount" v-if="comment.score > 0">{{comment.score}}<span class="percennt"></span></div>
        <div class="scroe" v-if="comment.score > 0">
          <span v-if="comment.score < 2"></span>
          <span v-else-if="comment.score <= 4"></span>
          <span v-else-if="comment.score <= 6"></span>
          <span v-else-if="comment.score <= 8"></span>
          <span v-else-if="comment.score <= 10"></span>
        </div>
        <div class="grade">
          <div class="grade-icon" ref="parentEl" @click.stop.prevent="setGrade">
            <div class="grade_progress" :style="{width: `${comment.score * 10}%`}"></div>
          </div>
          <!-- <div class="acturally">{{comment.score}}</div> -->
          <div class="comment"></div>
        </div>
        <div class="edit-input">
          <textarea @click="setFocus" ref="textareaEl" v-model="content" maxlength="50" :placeholder='placeholderText'></textarea>
          <div class="text-right">{{content.length}}/50</div>
        </div>
        <div class="flex-row" :class="{jusB: comment.is_self === 1}">
          <div class="edit-publish" @click="publish" :class="{active: content}">{{comment.reviewid ? '' : ''}}</div>
        </div>
        <div class="close" @click="showEdit = false"></div>
      </div>
    </div>
    <all-comment :ChangeObj='ChangeObj' v-if="showAll" @filmOperate='filmOperate' @changeDelete='changeDelete' @close='showAll = false'></all-comment>
    <prompt-box
      :dropBol="isDelete"
      @leftBtn="isDelete=false"
      leftTxt=""
      @rightBtn="confirmBtn"
      rightTxt=""
      contentTxt=""
    >
    </prompt-box>
  </div>
</template>
<script>
import CommentList from './film-comment-list'
import AllComment from './all-movie-comment'
import promptBox from '@/components/common/prompt-box'
export default {
  components: {
    CommentList,
    AllComment,
    promptBox
  },
  props: {
    details: {
      type: Object
    },
    reviewList: {
      type: Array
    },
    writeCount: {
      type: Number,
      default: 0
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  data () {
    return {
      comment: {
        score: 0
      },
      isDelete: false,
      showAll: false,
      ChangeObj: {
        count: '',
        obj: {}
      },
      content: '',
      showEdit: false,
      placeholderText: ''
    }
  },
  methods: {
    confirmBtn () {
      this.setDelete()
    },
    filmOperate ({index, operate}) {
      if (operate === 1) {
        if (this.$utils.isSpecialBrowser()) {
          this.$emit('filmOperate', {operate: 5})
        } else {
          this.showEdit = true
        }
      } else {
        this.$emit('filmOperate', {index, operate})
      }
    },
    setFocus () {
      setTimeout(() => {
        this.$refs.textareaEl.focus()
      }, 100)
    },
    // 
    setDelete () {
      let data = {
        reviewid: this.comment.reviewid,
        vod_id: this.details.vod_id
      }
      this.$api.post('DELETE_MOVIE_COMMENT', data, res => {
        this.changeDelete()
        this.$toast({
          message: '',
          duration: 1500
        })
        this.isDelete = false
        this.comment = {
          score: 0
        }
        this.content = ''
      })
    },
    // 
    changeDelete (index) {
      this.$emit('filmOperate', {index, operate: 4})
    },
    setEdit () {
      if (this.$utils.isSpecialBrowser()) {
        return this.$emit('filmOperate', {operate: 5})
      } else {
        this.showEdit = true
      }
    },
    reviewFabulous (e) {
      this.$emit('reviewFabulous', e)
    },
    editComment () {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      if (this.$utils.isSpecialBrowser()) {
        this.$emit('filmOperate', {operate: 5})
      } else {
        this.showEdit = true
      }
      this.comment = {
        score: 0
      }
    },
    toImage () {
      this.$router.push('/movie/image?vod_id=' + this.$route.params.id)
    },
    checkAll () {
      this.showAll = true
    },
    publish () {
      let data = {
        vod_id: this.$route.params.id,
        score: this.comment.score,
        content: this.content
      }
      if (!this.comment.score || this.comment.score * 1 === 0) {
        return this.$toast({
          message: '',
          duration: 1500
        })
      }
      if (!this.content || this.content.trim() === '') {
        return this.$toast({
          message: '',
          duration: 1500
        })
      }
      if (this.comment.reviewid) {
        data.reviewid = this.comment.reviewid
      }
      this.$api.post('MOVIE_REVIEW', data, res => {
        let txt = ''
        if (data.reviewid) {
          txt = ''
        }
        let obj = {
          avatar: this.commonState.userInfo.avatar,
          comment_time: parseInt(new Date().getTime() / 1000),
          content: this.content,
          fabulous: this.comment.fabulous ? this.comment.fabulous : 0,
          is_fabulous: this.comment.is_fabulous ? this.comment.is_fabulous : 0,
          is_self: 1,
          nickname: this.commonState.userInfo.nickname,
          reply: this.comment.reply ? this.comment.reply : 0,
          reviewid: res.data.reviewid,
          score: this.comment.score
        }
        this.comment = obj
        this.$toast({
          message: txt,
          duration: 1500
        })
        this.$emit('setList', obj)
        this.$nextTick(() => {
          this.showEdit = false
        })
        // 
        this.ChangeObj.obj = obj
        this.ChangeObj.type = 'change'
        this.ChangeObj.count++
      })
    },
    setGrade (e) {
      let progress = e.clientX - this.$utils.getElLeftPotion(this.$refs.parentEl)
      let width = this.$refs.parentEl.offsetWidth
      let percent = progress / width * 10
      let diff = percent - parseInt(percent)
      if (diff < 0.25) {
        percent = parseInt(percent)
      } else if (percent >= 0.25 && percent.percent > 0.75) {
        percent = parseInt(percent) + 0.5
      } else {
        percent = parseInt(percent) + 1
      }
      if (percent <= 1) {
        percent = 1
      }
      this.comment.score = percent
    },
    setMovie () {
      if (this.reviewList.length > 0 && this.details.is_review) {
        this.comment = JSON.parse(JSON.stringify(this.reviewList[0]))
        this.content = this.comment.content
      } else {
        this.comment = {
          score: 0
        }
        this.content = ''
      }
    }
  },
  watch: {
    'details.is_review' (flag) {
      if (flag) {
        this.comment = this.reviewList[0]
      } else {
        this.comment = {
          score: 0
        }
      }
    },
    writeCount () {
      this.showEdit = true
    },
    $route () {
      this.setMovie()
    }
  },
  mounted () {
    this.setMovie()
  }
}
</script>
<style lang="scss" scoped>
.film-comment {
  .currentUser {
    .grade {
      height: 30px;
    }
  }
  .title {
    padding: 0 30px;
    font-size: 32px;
    line-height: 45px;
    font-weight: 900;
    margin: 60px 0 20px;
  }
  .comment-area {
    background: #FFFFFF;
    box-shadow: -2px -2px 10px 0 #EBEAEF, 4px 4px 10px 0 #DADADA;
    border-radius: 27px;
    width: 686px;
    min-height: 268px;
    margin: 0 auto;
    padding-bottom: 14px;
    .top-logo {
      display: flex;
      padding: 40px 30px 25px;
      font-size: 28px;
      color: #666;
      .juejinbao {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        white-space: nowrap;
        .logo {
          width: 60px;
          height: 60px;
          overflow: hidden;
          margin-right: 10px;
          border-radius: 50%;
        }
        &:nth-child(1) {
          margin-right: 20px;
        }
        .active {
          color: #01b80f;
        }
      }
    }
    .grayBg {
      background: #f5f5f5;
      border-radius: 20px;
      margin: 0px 20px 0px;
      padding: 24px 21px 28px;
    }
    .comment-item {
      padding: 0px 20px 30px;
      position: relative;
      &:last-child {
        &::before {
          display: none;
        }
      }
      &::before {
        left: 20px;
        right: 20px;
        width: auto;
      }
      .edit {
        border: 1px solid #F4A82B;
        border-radius: 5px;
        width: 74px;
        height: 38px;
        line-height: 38px;
        text-align: center;
        color: #F4A82B;
        margin-left: 20px;
      }
      .left {
        height: 100%;
        // flex-grow: 1;
        align-self: flex-start;
        .avatar {
          width: 60px;
          height: 60px;
          border-radius: 50%;
          overflow: hidden;
          margin-right: 20px;
        }
      }
      .right {
        flex:1;
        .userInfo {
          align-items: flex-end;
          margin-bottom: 5px;
          line-height: 1.2;
          .nickname {
            text-align: left;
            margin-right: 20px;
            color: #666;
            &.is_self {
              color: #FF4112;
            }
          }
          .grade {
            flex: 1;
          }
          .digg_info {
            display: flex;
            align-items: flex-end;
            .digg-icon {
              width: 39px;
              height: 42px;
              margin-right: 8px;
            }
            .text {
              line-height: 34px;
            }
            &.active {
              .text {
                color: #ff4b1f
              }
            }
          }
        }
        .content {
          line-height: 42px;
          max-height: 126px;
          text-align: justify;
          font-size: 30px;
          overflow: hidden;
          text-overflow: -o-ellipsis-lastline;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          line-clamp: 3;
          /* autoprefixer: off */
          -webkit-box-orient: vertical;
          /* autoprefixer: on */
        }
        .others {
          text-align: left;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          margin-top: 5px;
          .update_time {
            color: #666;
            line-height: 38px;
            margin-bottom: 20px;
          }
          .edit, .delete {
            border: 1px solid #F4A82B;
            border-radius: 5px;
            width: 74px;
            height: 38px;
            line-height: 38px;
            text-align: center;
            color: #F4A82B;
            margin-left: 20px;
          }
          .delete {
            color: #ff4b1f;
            border-color: #ff4b1f;
          }
        }
      }
    }
    .edit-content {
      padding: 0 20px;
      margin-top: 10px;
      .generate {
        margin: 0;
        width: 238px;
      }
      .edit, .delete {
        color: #F4A82B;
        font-size: 28px;
        border: 1px solid #D9B056;
        line-height: 40px;
        border-radius: 32.5px;
        padding: 15px 36px 12px;
      }
    }
    .button {
      margin: 0 auto;
      border-radius: 6px;
      width: 323px;
      background: #F4A82B;
      border: 1px solid #D9B056;
      border-radius: 32.5px;
      height: 65px;
      color: #fff;
      &.no_review {
        margin-top: 30px;
      }
    }
  }
  .check-all {
    text-align: center;
    color: #999;
    padding: 32px;
  }
  .commentEdit {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba($color: #000000, $alpha: .6);
    .commentEditBox {
      position: absolute;
      top: 160px;
      left: 20px;
      right: 20px;
      background: #fff;
      border-radius: 30px;
      .close {
        width: 60px;
        height: 60px;
        background: url('../../../assets/img/task/close_circle.png') no-repeat center;
        background-size: 46px 46px;
        position: absolute;
        bottom: -100px;
        left: 50%;
        transform: translateX(-50%);
        -webkit-transform: translateX(-50%);
      }
      .gradeCount {
        line-height: 50px;
        text-align: center;
        font-size: 50px;
        color: #f4a82b;
        .percennt {
          font-size: 30px;
        }
      }
      .scroe {
        text-align: center;
        line-height: 50px;
        // color: #f4a82b;
        font-size: 36px;
      }
      .edit-title {
        max-width: 80%;
        text-align: center;
        margin: 0 auto;
        padding: 40px 0 45px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: 900;
        font-size: 36px;
        line-height: 50px;
      }
      .grade {
        height: 70px;
        .grade-icon {
          height: 72px;
          background: url('../../../assets/img/movie/grade.png') no-repeat left top;
          width: 370px;
          background-size: 370px;
          .grade_progress {
            height: 70px;
            background: url('../../../assets/img/movie/grade.png') no-repeat bottom left;
            background-size: 370px;
          }
        }
        .acturally {
          font-size: 36px;
          width: 40px;
        }
      }
      .tip {
        font-size: 32px;
        color: #666;
        margin-bottom: 45px;
        text-align: center;
      }
      .grade {
        justify-content: center;
      }
      .edit-input {
        margin-top: 40px;
        border-bottom: 1px solid #e5e5e5;
        border-top: 1px solid #e5e5e5;
        padding: 30px 30px 16px;
        color: #666;
        height: 350px;
        box-sizing: border-box;
        textarea {
          width: 100%;
          height: calc(100% - 56px);
          display: block;
          font-size: 32px;
          line-height: 1.5;
          border: 0;
        }
        .text-right {
          line-height: 40px
        }
      }
      .edit-publish {
        padding: 42px 0 38px;
        font-size: 36px;
        line-height: 50px;
        box-sizing: border-box;
        text-align: center;
        border-top: 1px solid #f5f5f5;
        margin: 0 auto;
        &.active {
          color: #F4A82B;
        }
      }
    }
  }
}
</style>



