<template>
  <div id="headerNav" class="flex-row jusC">
    <div class="leftDiv bgIcon flex-row" @click="back">
      <div class="leftIcon"></div>
    </div>
    <div class="title">{{msg ? msg : $route.meta.title}}</div>
    <div v-if='showShare' @click="headerShare" class="right"></div>
  </div>
</template>
<script>
export default {
  props: {
    msg: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showShare: false
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    headerShare () {
      this.$emit('headerShare')
    }
  },
  created () {
    if (this.$config.source_style < 6) {
      let paths = ['/saveimage', '/invite_friend', '/my/profit']
      for (let v of paths) {
        if (this.$route.path === v) {
          this.showShare = true
        }
      }
    }
  },
  mounted () {
    
  }
}
</script>
<style lang="scss" scoped>
#headerNav{
  height: 88px;
  position: fixed;
  display: flex;
  box-sizing: border-box;
  color: #222;
  left: 0;
  right: 0;
  z-index: 2;
  font-size: 34px;
  top: 0px;
  transform: translate(0,0,0);
  color: #fff;
  .leftDiv{
    position: absolute;
    left: 0;
    width: 100px;
    height: 100%;
    top: 0;
    background-image: url('../../assets/img/white_back.png');
    background-size: 20px 35px;
  }
  .right{
    height: 88px;
    line-height: 88px;
    font-size: 30px;
    position: absolute;
    right: 0;
    padding: 0 30px;
  }
}
</style>



