<template>
  <div id="film" ref="film" :class="{hasHeader: $utils.showNavBar(), isIos: $config.source_style === 6 && version}">
    <!--  -->
    <div class="hasData" v-if="details">
      <!--  -->
      <header-nav-ios v-if="$config.source_style === 6 && version" :msg="($utils.isWx() || $utils.isQQ()) ? ' ' : $utils.splitStr(details.vod_name, 10)"></header-nav-ios>
      <div class="film_video" :class="{isIos: $config.source_style === 6 && version}">
        <header-nav v-if="$config.source_style !== 6 || (!version)" :msg="($utils.isWx() || $utils.isQQ()) ? ' ' : $utils.splitStr(details.vod_name, 10)"></header-nav>
        <div class="noLogin" v-if="!commonState.login_bol">
          <div class="posC">
            <div class="text-center text"></div>
            <div class="login" @click="toLogin"></div>
          </div>
        </div>
        <!--  -->
        <div class="isWx" v-else-if="$utils.isWx() || $utils.isQQ()">
          <wechat-guild></wechat-guild>
        </div>
        <!-- app -->
        <div class="normal" :class="{isIos: $config.source_style === 6 && version}" v-else>
          <!-- m3u8 iOS -->
          <div class="wrapper" ref="video_Container" v-if="details.play_type === 'm3u8' && $config.source_style === 6 && version">

          </div>
          <!-- iframe -->
          <div class="video-content iframe" ref="iframeEl" v-if="details.play_type === 'iframe'" id="video">
          </div>
          <!-- m3u8 -->
          <div class="wrapper" v-if="details.play_type === 'm3u8' && $config.source_style === 7 || !this.version">
            <div class="video-content m3u8" id="m3u8Video" ref="m3u8Video">
            </div>
          </div>
          <!--  -->
          <transition :name='animation'>
            <div v-if="showHistory" class="watch-history flex-row">
              <div class="close iconSize" @click="closeJump">
                <img src="../../../assets/img/close.png" alt="">
              </div>
              <div class="text">{{$utils.getTimeText(parseInt(history.time))}}</div>
              <div class="jumpPlay" @click="jumpPlay"></div>
            </div>
          </transition>
          <!--  -->
          <!--  -->
          <div class="noplayUrl" v-if="details.vod_play_list.length === 0">
            <div class="no-title">
              <div class="text"></div>
              <div class="sec-text"></div>
              <div class="iconSize">
                <img src="../../../assets/img/movie/no-play-line.png" alt="">
              </div>
            </div>
          </div>
          <!--  -->
          <div class="autoPlay" v-if="showAutoplay && details.type_id_1 !== 1">
            <div class="close iconSize" @click="close(2)">
              <img src="../../../assets/img/close.png" alt="">
            </div>
            <span></span>
            <span v-if="details.type_id_1 === 2 || details.type_id_1 === 4 "></span>
            <span v-else></span>
            <div class="cancel" @click="toNext()"></div>
          </div>
          <!--  -->
          <div class="autoPlay" v-if="showAutoplay && details.type_id_1 === 1">
            <div class="close iconSize" @click="close(2)">
              <img src="../../../assets/img/close.png" alt="">
            </div>
            <span></span>
            <div class="cancel" @click="toWrite"></div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="speed" v-if="$config.source_style < 6" @click="toDownload">
        <div>APP5G200%</div>
        <div class="tryIt"></div>
      </div>
      <!--  -->
      <transition>
        <div class="flex-row adverShow" v-if="showAd && details.play_type === 'iframe'">
          <div class="messageIcon">
            <img src="../../../assets/img/my/message_active.png" alt="">
          </div>
          <div class="content color666"></div>
          <div class="close" @click="closeAd">
            <img src="../../../assets/img/movie/close_ad.png" alt="">
          </div>
        </div>
      </transition>
      <!--  -->
      <div class="film-intro">
        <div class="setUpdate" v-if="details.type_id_1 !== 1 && commonState.login_bol" @click="setShowTip">
          <div class="text flex-row active" v-if="details.is_subscription === 0"></div>
          <div class="text flex-row" v-else>
            <div class="icon flex-row iconSize">
              <img src="../../../assets/img/movie/update.png" alt="">
            </div>
            <div class="text"></div>
          </div>
        </div>
        <div class="film-title">{{$utils.splitStr(details.vod_name, 15)}}</div>
        <div class="film-type flex-row" v-if="details.vod_remarks">
          <div class="film-type-content">{{details.vod_remarks}}</div>
        </div>
        <!-- div. -->
        <div v-if="details.vod_time" class="film-class update-time">
          <span>{{$utils.timestampToStr(details.vod_time * 1000, 'Y-M-D')}}</span>
        </div>
        <div class="film-class flex-row jusB">
          <div class="film-main">
            <span v-if="details.vod_year > 1900">{{details.vod_year}}</span>
            <span v-if="details.vod_area">{{details.vod_area}}</span>
            <span v-if="details.vod_class">{{details.vod_class}}</span>
            <span v-if="details.vod_hits">{{details.vod_hits | setPeople}}</span>
          </div>
          <div class="right">
            <div class="flex-row" @click="showDetail = true">
              <div class="text"></div>
              <div class="arrowEl"></div>
            </div>
          </div>
        </div>
        <!-- <div class="film-update">{{details.}}</div> -->
        <div class="film-desc" v-if="commonState.login_bol && details.vod_content">
          <div class="film-desc-left">{{details.vod_content}}</div>
          <div class="right">
            <!-- <div class="flex-row" @click="showDetail = true">
              <div class="text"></div>
              <div class="arrowEl"></div>
            </div> -->
          </div>
        </div>
      </div>
      <!--  -->
      <div class="noLoginPlayLine" v-if="!commonState.login_bol">
        <div class="title"></div>
        <div class="noplay flex-row">
          <div class="login-text"></div>
          <div class="flex-row login-btn" @click="toLogin">
            <div class="login-btn-text"></div>
            <div class="arrowEl"></div>
          </div>
        </div>
      </div>
      <div v-show="commonState.login_bol">
        <!--  -->
        <div class="count">
          <div class="count-digg flex-row" :class="{active: details.is_fabulous === 1}" @click="setDigg(0)">
            <div class="digg-icon iconSize" >
              <img v-if="details.is_fabulous === 1" src="../../../assets/img/details/digg_active.png" alt="">
              <img v-else src="../../../assets/img/details/digg.png" alt="">
            </div>
            <div class="count-text">{{details.fabulous_count ? details.fabulous_count : 0 | setPeople}}</div>
            <div class="redColor" :class="{show: is_digg > 0, active: is_digg > 1}">+1</div>
          </div>
          <div class="count-digg flex-row">
            <div class="comment-icon iconSize" @click="setDigg(1)">
              <img src="../../../assets/img/details/comment.png" alt="">
            </div>
            <div class="count-text">{{details.comment_count ? details.comment_count : 0 | setPeople}}</div>
          </div>
          <div class="count-digg flex-row" @click="setDigg(2)">
            <span></span>&nbsp;|&nbsp;<span></span>
          </div>
        </div>
        <!--  -->
        <div class="play-source" v-if="isHtml">
          <div class="title"></div>
          <div class="play-source-list">
            <div class="item" v-for="(item, index) in details.play_list" :class="{active: playLineIndex === index}" :key="index" @click="setPlayList(index)">{{index}}</div>
          </div>
        </div>
        <!--  -->
        <div class="play-line isWx" v-if="$utils.isWx()">
          <div class="video-line">
            <div class="title"></div>
            <div class="noplay">APP</div>
          </div>
        </div>
        <div class="play-line" v-if="details.playSource && details.playSource.length > 0 && !$utils.isWx()">
          <div class="video-line">
            <div class="title"></div>
            <div class="text f24 color999"></div>
            <div class="video-play-list">
              <!-- <div class="video-source-item" @click="setVideoResource(index)" v-if="item[0]" :class="{active: index === activeSource}" v-for="(item, index) in this.details.playSource" :key="index">{{item[0].source ? item[0].source : '' + (index + 1)}}</div> -->
              <div class="video-source-item" @click="setVideoResource(index)" v-if="item[0]" :class="{active: index === activeSource}" v-for="(item, index) in this.details.playSource" :key="index">{{'' + (index + 1)}}</div>
            </div>
          </div>
          <!-- <div class="top-line flex-row" v-else-if="details.type_id_1 === 2">
            <div class="title"></div>
            <div class="text text_vod_marks">{{details.vod_remarks}}</div>
            <div class="more-line" @click="setLineMore">
              <div class="text">/</div>
              <div class="arrowEl"></div>
            </div>
          </div>
          <div class="top-line flex-row" v-else>
            <div class="title"></div>
            <div class="text text_vod_marks">{{details.vod_remarks}}</div>
            <div class="more-line" @click="setLineMore">
              <div class="text">/</div>
              <div class="arrowEl"></div>
            </div>
          </div> -->
          <div class="play-resource" ref="playSourceBox">
            <div class="play-item"  :class="{active: activeIndex === index}" :ref="activeIndex === index ? 'playSource' : ''" @click="setActiveIndex(index, item.url)" v-for="(item, index) in details.playSource[activeSource]" :key="index">{{item.title}}</div>
          </div>
        </div>
        <!--  -->
        <film-comment :details='details' :writeCount="writeCount" @filmOperate="filmOperate" v-if="showComment" @reviewFabulous="reviewFabulous" @setList="setList" :reviewList="reviewList" @checkAll="checkAll"></film-comment>
        <!--  -->
        <div class="recomend" v-if="recommendList.length > 0">
          <div class="title"></div>
          <rcommmend :list="recommendList" :isRefresh="isRefresh"></rcommmend>
        </div>
        <!--  -->
        <div class="screen_behind recomend" v-if="articleList.length > 0">
          <div class="title flex-row jusB">
            <div class="left">{{details.type_id_1 === 1 ? '&' : ''}}</div>
            <div class="right flex-row" v-if="articleList.length > 5" @click="checkMore('article')">
              
              <div class="arrowEl"></div>
            </div>
          </div>
          <news-list :list="articleList.slice(0, 5)"></news-list>
        </div>
        <hot-comment v-if='details' @upperLevel="upperLevel" :clickNum='clickNum' :details="details" :page="page" @pageOne="page=1" @setPage="setPage" @total="getTotal" ></hot-comment>
      </div>
    </div>
    <!--  -->
    <div v-else>
      <list-loading></list-loading>
    </div>
    <intro-detail v-if="showDetail" :details="details" @closeDetail='closeDetail'></intro-detail>
    <more-line v-if="showMoreLine" @closeDetail='closeMoreLine' :activeSource="activeSource" :faActiveIndex2="activeIndex" :details='details' @switchResource="switchResource"></more-line>
    <confirm v-if="showConfirm" @setFn="setFn" :config="config"></confirm>
    <share-lead :isMovie="1" v-if="movieGuild"></share-lead>
    <prompt-box
      :dropBol="showTip"
      @leftBtn="showTip=false"
      @rightBtn="confirmBtn"
      leftTxt=""
      :rightTxt="rightText"
      :contentTxt="confirmText"
    >
    </prompt-box>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav-white'
import DPlayer from 'dplayer'
import HLS from 'hls.js'
import {mapState, mapMutations} from 'vuex'
import FilmComment from '@/components/movie/common/film-comment'
import List from '@/components/movie/common/list'
import HotComment from '@/components/movie/common/hot-comment'
import IntroDetail from '@/components/movie/common/intro-detail'
import MoreLine from '@/components/movie/common/more-line'
import Rcommmend from '@/components/movie/common/recommend'
import NewsList from '@/components/common/news-list'
import WechatGuild from '@/components/movie/common/play-source'
import PromptBox from '@/components/common/prompt-box'
import ListLoading from '@/components/movie/common/video-loading'
import Confirm from '@/components/movie/common/confirm-bottom'
import ShareLead from '@/components/home/article-lead'
import HeaderNavIos from '@/components/task/header-nav'
export default {
  components: {
    HeaderNav,                  //  
    FilmComment,                //  
    List,                       //  
    Confirm,                    //  
    HotComment,                 //  
    IntroDetail,                //  
    MoreLine,                   //  
    Rcommmend,                  //  
    NewsList,                   //  
    WechatGuild,                //  
    PromptBox,                  //  
    ListLoading,                 //  
    ShareLead,
    HeaderNavIos
  },
  data () {
    return {
      isRefresh: 0,
      version: false,
      movieGuild: false,
      showguild: false,
      hasShowRecord: false,
      is_self: 1,
      writeCount: 0,
      config: {
        title: 'APP',
        cancel: '',
        confirm: ''
      },
      adverAnimation: '',
      showAutoplay: false,
      showTip: false,
      showAd: true,
      beforePath: '',
      rightText: '',
      confirmText: '',
      recommendList: [],       //  
      articleList: [],         //  
      activeSource: 0,
      currentUrl: '',          //  
      playLineIndex: '',
      isHtml: false,
      showConfirm: false,
      showComment: false,
      showDetail: false,        //  
      showMoreLine: false,      //  
      showHistory: false,
      reviewList: [],
      playUrl: '',
      is_digg: 0,               //   0 1  2 
      is_set_digg: false,       //  
      clickNum: 0,              //  
      page: 1,
      list: [],
      activeIndex: 0,
      animation: 'RightList',                  //  leftList
      details: null,
      history: {},
      videoTitle: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: ''
      }
    }
  },
  computed: {
    ...mapState({
      movieState: state => state.movie,
      commonState: state => state.common
    })
  },
  methods: {
    ...mapMutations([
      'movie_setHistory',
      'common_login_box'
    ]),
    close (e) {
      if (e === 2) {
        this.showAutoplay = false
      }
    },
    setPage (e) {
      this.page = e
    },
    //  
    toNext () {
      if (this.activeIndex < this.details.playSource[this.activeSource].length) {
        this.activeIndex++
        this.setPlay(this.details.playSource[this.activeSource][this.activeIndex].url)
        this.$nextTick(() => {
          this.$refs.playSourceBox.scrollLeft = this.$refs.playSource[0].offsetLeft
        })
      }
      this.showAutoplay = false
    },
    toWrite () {
      this.writeCount++
      this.$nextTick(() => {
        if (document.getElementsByClassName('film-comment')[0]) {
          window.scrollTo(0, this.$utils.getElposition(document.getElementsByClassName('film-comment')[0]) - document.getElementsByClassName('film_video')[0].clientHeight)
          this.showAutoplay = false
          this.showAutoplay = false
        }
      })
    },
    setFn ({actions}) {
      if (actions === 'cancel') {
        this.showConfirm = false
      } else if (actions === 'confirm') {
        this.$utils.openDownUrl()
      }
    },
    toLogin () {
      this.common_login_box(true)
    },
    //  
    setVideoResource (index) {
      this.activeSource = index
      this.setPlay(this.details.playSource[this.activeSource][0].url)
    },
    setShowTip () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (this.details.is_subscription === 1) {
        this.confirmText = ''
        this.rightText = this.details.type_id_1 === '2' ? '' : ''
      } else {
        this.confirmText = ''
        this.rightText = this.details.type_id_1 === '2' ? '' : ''
      }
      this.showTip = true
    },
    //  
    confirmBtn () {
      let data = {
        type: 'mac',
        vod_id: this.$route.params.id
      }
      let url = ''
      if (this.details.is_subscription === 1) {
        url = 'MOVIE_SUBSCRIBE_CANCEL'
        data.id = this.details.subscription_id
      } else {
        url = 'MOVIE_SUBSCRIBE'
      }
      this.$api.post(url, data, res => {
        this.details.is_subscription = this.details.is_subscription === 1 ? 0 : 1
        if (this.details.is_subscription === 1) {
          this.$toast({
            message: '',
            duration: 1500
          })
          this.details.subscription_id = res.data.id
        } else if (this.details.is_subscription === 0) {
          this.$toast({
            message: '',
            duration: 1500
          })
        }
        this.showTip = false
      })
    },
    setActiveIndex (index, url) {
      this.activeIndex = index
      this.setPlay(url)
    },
    //  
    checkMore () {
      this.$router.push(`/movie/recommend/${this.$route.params.id}`)
    },
    //  
    reviewFabulous (index) {
      let obj = this.reviewList[index]
      obj.is_fabulous = 1
      obj.fabulous++
      this.$set(this.reviewList, index, obj)
    },
    //  html
    setPlayList (index) {
      this.playLineIndex = index
      this.playUrl = this.details.play_list[this.playLineIndex] + this.currentUrl.replace('http:', 'https:')
      this.setPlay(this.playUrl)
    },
    //  
    setLineMore () {
      this.showMoreLine = true
    },
    //  
    getDetail () {
      if (this.$config.source_style < 6) {
        if (this.$route.params.id) {
          this.$router.replace({
            path: '/movie/detail?id=' + this.$route.params.id
          })
        }
        return
      }
      if (!this.$route.params.id) {
        return
      }
      this.showguild = false
      this.showAutoplay = false
      this.hasShowRecord = false
      this.getArticle()
      this.getRecommend()
      let div = document.createElement('div')
      this.$api.get('MOVIE_DETAIL', {vod_id: this.$route.params.id}, res => {
        this.getMovieList()
        this.activeSource = 0
        this.activeIndex = 0
        //  res.data.vod_content = ''
        this.details = res.data
        div.innerHTML = this.details.vod_content
        this.details.vod_content = div.innerText
        if (this.details.play_list) {
          for (let i in this.details.play_list) {
            this.playLineIndex = i
            break
          }
        }
        this.details.vod_pic = this.$utils.getPlayUrl(this.details.vod_pic)
        this.setTitle(this.details.type_id_1)
        this.details.playSource = this.details.vod_play_list
        if (this.details.playSource[0][0]) {
          this.details.video_url = this.details.playSource[0][0].url
          this.details.vod_bd = this.details.playSource[0][0].title
        }
        this.details.play_type = ''
        this.currentUrl = this.details.video_url
        if (this.commonState.login_bol) {
          this.$nextTick(() => {
            this.setPlay(this.details.video_url ? this.details.video_url : '')
            window.scrollTo(0, 0)
          })
        }
      })
    },
    getRecommend () {
      this.$api.get('MOVIE_RECOMMEND_DETAIL', {vod_id: this.$route.params.id}, res => {
        this.recommendList = res.data
        this.isRefresh++
      })
    },
    //  
    getMovieList () {
      this.showComment = false
      let data = {
        page: 1,
        vod_id: this.$route.params.id
      }
      this.$api.get('MOVIE_REVIEW_LIST', data, res => {
        if (res.data.data.length > 0 && res.data.data[0].is_self === 1) {
          this.$set(this.details, 'is_review', 1)
        } else {
          this.$set(this.details, 'is_review', 0)
        }
        this.reviewList = res.data.data
        this.$nextTick(() => {
          this.showComment = true
        })
      })
    },
    //    2  3   4 5
    filmOperate ({index, operate}) {
      if (operate === 2) {
        let data = {
          vod_id: this.$route.params.id,
          reviewid: this.reviewList[index].reviewid
        }
        this.$api.post('DELETE_MOVIE_COMMENT', data, res => {
          //  
          this.details.is_review = 0
          this.reviewList.splice(index, 1)
        })
        //  
      } else if (operate === 3) {
        let obj = this.reviewList[index]
        this.$api.post('MOVIE_COMMENT_FABULOUS', {vod_id: this.$route.params.id, reviewid: obj.reviewid}, res => {
          obj.fabulous++
          obj.is_fabulous = 1
        })
        //  
      } else if (operate === 4) {
        this.reviewList.splice(index, 1)
        this.details.is_review = 0
      } else if (operate === 5) {
        this.showConfirm = true
      }
    },
    //  
    getArticle () {
      this.$api.get('MOVIE_ARTICLE', {page: 1, vod_id: this.$route.params.id}, res => {
        this.articleList = res.data.data
      })
    },
    //  
    setList (e) {
      if (this.reviewList.length > 0 && e.reviewid * 1 === this.reviewList[0].reviewid * 1) {
        this.$set(this.reviewList, 0, e)
      } else {
        if (this.reviewList.length === 0) {
          this.reviewList = [e]
        } else {
          this.reviewList = [e, ...this.reviewList]
        }
        this.details.is_review = 1
      }
    },
    //  
    checkAll () {

    },
    //  
    setTitle (id) {
      document.title = this.$utils.splitStr(this.details.vod_name, 10)
    },
    //  
    setDplayer (url) {
      if (this.$config.source_style === 7 || !this.version) {
        this.dp = new DPlayer({
          container: this.$refs.m3u8Video,
          lang: 'zh-cn',
          autoHideTimer: '3000',
          video: {
            url: url,
            pic: this.details.vod_pic,
            type: 'customHls',
            autoplay: true,
            mutex: true,
            customType: {
              'customHls': function (video, player) {
                const hls = new HLS()
                hls.loadSource(video.src)
                hls.attachMedia(video)
              }
            }
          }
        })
        this.dp.on('canplay', () => {
          this.dp.play()
        })
        this.dp.on('loadstart', () => {

        })
        let count = 0
        this.dp.on('timeupdate', () => {
          count++
          if (this.dp.video.currentTime >= 120 && this.dp.video.currentTime <= (this.dp.video.duration - 600)) {
            this.showAutoplay = false
            if (count > 5) {
              count = 0
            }
            //  
          } else if (this.dp.video.currentTime > (this.dp.video.duration - 600)) {
            if (this.details.type_id_1 === 1 && !this.hasShowRecord) {
              this.showAutoplay = true
              this.hasShowRecord = true
              setTimeout(() => {
                this.showAutoplay = false
              }, 7000)
            }
            if (this.dp.video.currentTime > (this.dp.video.duration - 10) && !this.hasShowRecord) {
              if (this.details.type_id_1 !== 1) {
                if (this.details.playSource[this.activeSource]) {
                  if (this.activeIndex < this.details.playSource[this.activeSource].length) {
                    this.showAutoplay = true
                  }
                }
              }
            }
            if (this.details.type_id_1 !== 1 && this.dp.video.currentTime > (this.dp.video.duration - 3)) {
              if (this.showAutoplay) {
                this.toNext()
              }
            }
          }
        })
        this.dp.on('fullscreen', () => {
          if (this.$config.source_style === 6) {
            window.plus.screen.lockOrientation('landscape') //  
            window.plus.navigator.setFullscreen(true)
          } else if (this.$config.source_style === 7) {
            //  setTimeout(() => {
            //    this.windowFull(1)
            //  }, 1000)
          }
          setTimeout(() => {
            if (this.$config.source_style === 7) {
              this.dp.controller.hide()
            }
          }, 3000)
        })
        let el = document.getElementsByTagName('video')
        if (el) {
          this.$utils.setfullScreen(el[0])
          if (this.$config.source_style === 6) {
            this.dp.controller.hide()
            el[0].setAttribute('controls', 'controls')
          }
        } else {
          this.$utils.setfullScreen(document.body)
        }
        this.dp.on('fullscreen_cancel', () => {
          if (this.$config.source_style === 6) {
            window.plus.screen.lockOrientation('portrait') //    
            window.plus.navigator.setFullscreen(false)
          } else if (this.$config.source_style === 7) {
            //  this.windowFull(0)
          }
        })
        this.dp.container.addEventListener('click', () => {
          
        })
        this.dp.container.addEventListener('touchend', () => {
          if (this.hideTimer) {
            clearTimeout(this.hideTimer)
          }
          this.hideTimer = setTimeout(() => {
            if (this.$config.source_style === 7) {
              this.dp.controller.hide()
            }
          }, 3000)
        })
      } else {
        this.details.play_type = 'm3u8'
        if (this.$refs.video_Container) {
          if (document.getElementById('iOSVideo')) {
            this.video = document.getElementById('iOSVideo')
            this.video = this.video.parentNode.removeChild(this.video)
          }
          this.video = document.createElement('div')
          this.video.id = 'iOSVideo'
          this.$refs.video_Container.appendChild(this.video)
          this.$utils.plusReady((plus) => {
            this.videoPlayer = new plus.video.VideoPlayer('iOSVideo', {
              src: url,
              poster: this.details.vod_pic,
              position: 'absolute',
              top: 0
            })
          })
        }
      }
    },
    windowFull (pr) {
      var main = window.plus.android.runtimeMainActivity()
      var windowMe = main.getWindow()
      window.plus.android.importClass(windowMe)
      var decorView = windowMe.getDecorView()
      window.plus.android.importClass(decorView)
      var View = window.plus.android.importClass('android.view.View')
      if (pr === 1) {
        decorView.setSystemUiVisibility(
          View.SYSTEM_UI_FLAG_LAYOUT_STABLE |
          View.SYSTEM_UI_FLAG_LAYOUT_HIDE_NAVIGATION |
          View.SYSTEM_UI_FLAG_LAYOUT_FULLSCREEN |
          View.SYSTEM_UI_FLAG_HIDE_NAVIGATION |
          View.SYSTEM_UI_FLAG_FULLSCREEN |
          View.SYSTEM_UI_FLAG_IMMERSIVE_STICKY
        )
      } else if (pr === 2) {
        decorView.setSystemUiVisibility(
          View.SYSTEM_UI_FLAG_LAYOUT_STABLE |
          View.SYSTEM_UI_FLAG_LAYOUT_FULLSCREEN |
          View.SYSTEM_UI_FLAG_FULLSCREEN |
          View.SYSTEM_UI_FLAG_IMMERSIVE_STICKY
        )
      } else if (pr === 3) {
        decorView.setSystemUiVisibility(
          View.SYSTEM_UI_FLAG_LAYOUT_STABLE |
          View.SYSTEM_UI_FLAG_LAYOUT_HIDE_NAVIGATION |
          View.SYSTEM_UI_FLAG_HIDE_NAVIGATION |
          View.SYSTEM_UI_FLAG_IMMERSIVE_STICKY
        )
      } else if (pr === 0) {
        decorView.setSystemUiVisibility(View.SYSTEM_UI_FLAG_IMMERSIVE_STICKY)
      }
    },
    //  
    setPlay (url) {
      this.details.play_type = ''
      this.currentUrl = url
      this.isHtml = false
      this.videoPlayer && typeof this.videoPlayer.close === 'function' && this.videoPlayer.close()
      if (document.getElementById('iOSVideo')) {
        var video = document.getElementById('iOSVideo')
        video.parentNode.removeChild(video)
      }
      if (this.currentUrl.indexOf('.m3u8') === -1) {
        if (this.dp) {
          this.dp.destroy()
        }
      }
      if (this.$refs.iframeEl) {
        let beforeIframe = this.$refs.iframeEl.getElementsByTagName('iframe')
        if (beforeIframe[0]) {
          this.$refs.iframeEl.removeChild(beforeIframe[0])
        }
      }
      if (url.indexOf('.html') > -1 || url.indexOf('.shtml') > -1) {
        this.playUrl = this.details.play_list[this.playLineIndex] + this.currentUrl.replace('http:', 'https:')
        this.isHtml = true
        this.details.play_type = 'iframe'
      } else if (url.indexOf('m3u8') > -1) {
        if (this.dp) {
          this.dp.destroy()
        }
        this.playUrl = url
        this.details.play_type = 'm3u8'
        this.isHtml = false
        if (this.$config.source_style === 7 || (!this.version)) {
          this.timer = setInterval(() => {
            if (this.$refs.m3u8Video) {
              clearInterval(this.timer)
              this.timer = null
              this.setDplayer(url)
              return 0
            }
          }, 50)
        } else if (this.$config.source_style === 6) {
          this.timer = setInterval(() => {
            if (this.$refs.video_Container) {
              clearInterval(this.timer)
              this.timer = null
              this.setDplayer(url)
              return 0
            }
          }, 50)
          // this.setDplayer(url)
        }
      } else if (url.indexOf('pan.jiningwanjun.com') > -1) {
        this.dp = null
        this.playUrl = url
        this.play_type = 'iframe'
        this.isHtml = false
      } else if (url.indexOf('.mp4') > -1) {
        this.playUrl = this.currentUrl
      } else if (url.indexOf('http') === -1) {
        //  http
        this.isHtml = false
        if (url.indexOf('type=Q8') > -1 || url.indexOf('type=Q4') > -1) {
          //  
          this.playUrl = 'http:// j.zz22x.com/jx/?url=' + url
        } else if (url.indexOf('27pan') > -1) {
          //  27  
          this.playUrl = 'https:// api2.my230.com/?vid=' + url
        } else if (url.indexOf('o0o') > -1 || url.indexOf('mic') > -1 || url.indexOf('o9o') > -1 || url.indexOf('oooo') > -1 || url.indexOf('o0o') > -1) {
          //  64
          this.playUrl = 'http:// api.wz64.com/jx/?url=' + url
        } else if (this.$reg.KUYU.test(url)) {
          //  kuyuzy1
          this.playUrl = 'http:// www.onln.cn/getEmbedTv/' + url
        } else {
          //  
          this.playUrl = 'http:// bbkdj.vicp.cc/index.php?url=' + url
        }
        this.details.play_type = 'iframe'
      //   
      } else {
        this.isHtml = false
        this.playUrl = this.currentUrl
        this.details.play_type = 'iframe'
      }
      //  src
      if (this.details.play_type === 'iframe') {
        this.iframeTimer = setInterval(() => {
          if (this.$refs.iframeEl) {
            clearInterval(this.iframeTimer)
            this.iframeTimer = null
            this.setIframeELPLay()
          }
        }, 30)
      }
    },
    setIframeELPLay () {
      let beforeIframe = this.$refs.iframeEl.getElementsByTagName('iframe')
      if (beforeIframe[0]) {
        this.$refs.iframeEl.removeChild(beforeIframe[0])
      }
      let iframe = document.createElement('iframe')
      iframe.setAttribute('allowfullscreen', true)
      iframe.setAttribute('allowtransparency', true)
      iframe.setAttribute('frameborder', 0)
      iframe.setAttribute('src', this.playUrl)
      iframe.className = 'video'
      this.$refs.iframeEl.appendChild(iframe)
      let el = document.getElementsByTagName('iframe')
      if (el) {
        this.$utils.setfullScreen(el[0])
      } else {
        this.$utils.setfullScreen(document.body)
      }
    },
    //  
    upperLevel () {
      this.backToIndex()
    },
    backToIndex () {
      //  400ms
      if (this.$route.matched.length < 1) {
        this.$router.replace('/')
      } else {
        this.$router.go(-1)
      }
    },
    //  
    switchResource ({activeIndex, activeIndex1, activeIndex2}) {
      this.activeSource = activeIndex
      if (typeof activeIndex2 === 'number') {
        activeIndex1 = activeIndex1 > 0 ? activeIndex1 : 0
        this.activeIndex = activeIndex1 * 50 + activeIndex2
        this.setPlay(this.details.playSource[this.activeSource][this.activeIndex].url)
        this.showMoreLine = false
        this.$nextTick(() => {
          this.$refs.playSourceBox.scrollLeft = this.$refs.playSource[0].offsetLeft
        })
      }
    },
    //  
    getTotal (total) {
      this.details.comment_count = total
      this.pageNum = Math.ceil(this.total / 10)
    },
    //  
    setShowHistory () {
      if (this.details.play_type === 'm3u8') {
        let flag = true
        if (this.movieState.playHistory.film) {
          for (let v of this.movieState.playHistory.film) {
            if (v.id === this.$route.params.id) {
              this.showHistory = true
              flag = false
              this.history = v
              this.$nextTick(() => {
                this.animation = 'leftList'
                setTimeout(() => {
                  this.showHistory = false
                }, 7000)
              })
            }
          }
        }
        if (flag) {
          this.showHistory = false
        }
      }
    },
    //  
    closeJump () {
      this.showHistory = false
    },
    setDigg (index) {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      if (index === 0) {
        if (this.details.is_fabulous === 0) {
          this.$api.post('MOVIE_FABULOUS', {vod_id: this.$route.params.id}, res => {
            this.is_digg = 1
            this.$nextTick(() => {
              this.is_digg = 2
              setTimeout(() => {
                this.details.is_fabulous = 1
                this.$set(this.details, 'fabulous_count', this.details.fabulous_count ? this.details.fabulous_count + 1 : 1)
                this.is_digg = 0
              }, 400)
              //  this.is_set_digg = true
            })
          })
        } else {
          this.$toast({
            message: '',
            duration: 1500
          })
        }
      } else if (index === 1) {
        this.$nextTick(() => {
          var scrollHeight = this.$utils.getElposition(document.getElementById('hot-comment'))
          document.getElementById('app').scrollTop = scrollHeight
          scrollHeight -= 250
          window.scrollTo(0, scrollHeight)
          setTimeout(() => {
            scrollHeight = this.$utils.getElposition(document.getElementById('hot-comment'))
            window.scrollTo(0, scrollHeight)
          }, 200)
        })
      } else if (index === 2) {
        this.$router.push('/AwardFeedback?from=movie')
      }
    },
    //  
    jumpPlay () {
      if (this.details.play_type === 'm3u8') {
        this.dp.seek(this.history.time)
        this.dp.play()
      }
      this.showHistory = false
    },
    //  
    toDownload () {
      this.$utils.openDownUrl()
    },
    closeAd () {
      this.adverAnimation = 'topAdver'
      this.$nextTick(() => {
        this.showAd = false
      })
    },
    //  
    closeDetail () {
      this.showDetail = false
    },
    //  
    closeMoreLine () {
      this.showMoreLine = false
    },
    //  
    upList () {
      if (this.$utils.scrollHeight() <= 200 && this.commonState.isLoading === 0) {
        this.page++
      }
      if (document.getElementsByClassName('film-comment')[0]) {
        if (this.$utils.getScrollTop() > (this.$utils.getElposition(document.getElementsByClassName('film-comment')[0]) - document.getElementsByClassName('film_video')[0].clientHeight) && !this.showguild) {
          this.setShowGuild()
          this.showguild = true
        }
      }
    },
    setShowGuild () {
      if (!this.commonState.login_bol) {
        return
      }
      if (!this.$utils.cache('movieGuild') || new Date().getTime() > this.$utils.cache('movieGuild')) {
        this.showguild = true
        this.movieGuild = true
        setTimeout(() => {
          this.movieGuild = false
        }, 3000)
        this.$utils.cache('movieGuild', new Date(this.$utils.timestampToStr(new Date().getTime(), 'Y/M/D') + ' 23:59:59').getTime())
      }
    },
    resetData () {
      this.isHtml = false
      this.showConfirm = false
      //  this.showComment = false
      this.showDetail = false        //  
      this.showMoreLine = false      //  
      this.showHistory = false
    }
  },
  beforeRouteLeave (to, from, next) {
    this.videoPlayer && this.videoPlayer.close()
    if (document.getElementById('iOSVideo')) {
      var video = document.getElementById('iOSVideo')
      video.parentNode.removeChild(video)
    }
    if (this.showDetail) {
      this.showDetail = false
      next(false)
    } else if (this.showMoreLine) {
      this.showMoreLine = false
      next(false)
    } else {
      window.removeEventListener('scroll', this.upList)
      if (to.name !== 'AwardFeedback') {
        if (this.dp) {
          this.dp.destroy()
        }
        if (this.details) {
          this.details.play_type = ''
        }
      }
      next()
    }
  },
  mounted () {
    this.$utils.cache('id', this.$route.params.id)
    this.getDetail()
    if (this.$config.source_style === 6) {
      this.$utils.plusReady((plus) => {
        let version = plus.os.version
        let versionArr = version.split('.')
        if (versionArr[0] > 10) {
          this.version = true
        } else {
          this.version = false
        }
      })
    }
  },
  activated () {
    if (!this.commonState.login_bol && this.$config.source_style >= 6 && !this.$utils.isWx()) {
      return this.common_login_box(true)
    }
    this.showguild = false
    this.resetData()
    window.removeEventListener('scroll', this.upList)
    window.addEventListener('scroll', this.upList)
  },
  watch: {
    //  idid
    '$route.params.id' (id) {
      this.videoPlayer && this.videoPlayer.close()
      if (this.$config.source_style < 6) {
        this.showComment = false
        this.getDetail()
        return
      }
      if (this.dp) {
        this.dp.destroy()
      }
      this.details.play_type = ''
      if (id) {
        if (this.$utils.cache('id') !== id) {
          this.getDetail()
        } else {
          this.setPlay(this.currentUrl)
        }
        this.$utils.cache('id', id)
      }
    },
    showAd (flag) {
      if (flag) {
        setTimeout(() => {
          this.adverAnimation = ''
          this.showAd = false
        }, 3000)
      }
    },
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.getDetail()
      }
      if (flag) {
        if (this.$refs.film.getElementsByTagName('iframe')[0]) {
          let obj = this.$refs.film.getElementsByTagName('iframe')[0]
          obj.parentNode.removeChild(obj)
        }
        if (this.dp) {
          this.dp.destroy()
        }
      }
    },
    showHistory (flag) {
      this.leftList = flag ? 'leftList' : 'RightList'
    }
  }
}
</script>
