  .autoPlay {
    position: absolute;
    left: 0;
    bottom: 100px;
    z-index: 100;
    color: #fff;
    display: flex;
    align-items: center;
    background: rgba($color: #666, $alpha: .8);
    height: 70px;
    padding: 0 40px 0 0px;
    line-height: 1.2;
    font-size: 30px;
    .cancel {
      color: #FF7F18;
      margin-left: 20px;
    }
    .close {
      display: flex;
      align-items: flex-start;
      padding: 20px;
      width: 25px;
      height: 25px;
    }
    border-radius: 0 35px 35px 0;
  }
  //  
  .count {
    display: flex;
    padding: 30px 20px;
    flex-flow: row nowrap;
    justify-content: space-between;
    .count-digg {
      display: flex;
      align-items: center;
      width: 168px;
      height: 58px;
      border: 1px solid #979797;
      border-radius: 30px;
      color: #666;
      &:last-child {
        border-color: #FF4112;
        color: #FF4112;
      }
      position: relative;
      &.active {
        color: #FF4112;
      }
      .redColor {
        position: absolute;
        top: -100px;
        display: none;
        &.show {
          display: block;
          transition: all .4s ease;
        }
        &.active {
          top: 0px;
          animation: diggFn 200ms linear;
        }
        @keyframes diggFn {
          from {
            transform: translateY(-60px);
            -webkit-transform: translateY(-60px);
          }
          to {
            transform: translateY(0);
            -webkit-transform: translateY(0px);
          }
        }
      }
      .iconSize {
        width: 39px;
        height: 42px;
        margin-right: 10px;
        .comment-icon {
          width: 48px;
          height: 48px;
        }
      }
    }
  }
  .noLoginPlayLine {
    margin-top: 30px;
    .title {
      font-size: 32px;
      line-height: 45px;
      margin-bottom: 12px;
      padding: 0 20px;
      font-weight: 900
    }
    .noplay {
      background: #F5F5F5;
      height: 95px;
      font-size: 32px;
      .login-btn {
        color: #6BA5D8;
        margin-left: 17px;
      }
    }
  }
  //  
  .play-line {
    border-top: 1px solid #EBEAEF;
    border-bottom: 1px solid #EBEAEF;
    padding: 56px 20px 30px;
    &.isWx {
      padding: 0 20px;
      .title {
        padding: 30px 0 15px;
      }
      .noplay {
        height: 90px;
        line-height: 90px;
        padding: 0 30px;
        background: #f5f5f5;
        margin-left: -20px;
        margin-right: -20px;
      }
    }
    .top-line {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .title {
        font-size: 32px;
        color: #333333;
        font-weight: 900;
        margin-right: 24px;
      }
      .text {
        font-size: 28px;
        flex: 1;
        text-align: left;
        line-height: 38px;
        min-width: 0;
        box-sizing: border-box;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #666;
      }
      .text_vod_marks {
        padding-right: 50px;
      }
      .more-line {
        font-size: 26px;
        display: flex;
        .text {
          color: #6BA5D8;
        }
        color: #6BA5D8;
        align-items: center;
      }
    }
    .video-line {
      .title {
        font-size: 32px;
        color: #333333;
        font-weight: 900;
        margin-right: 24px;
        line-height: 45px;
      }
      .text {
        line-height: 34px;
        margin-top: 5px;
      }
      .video-play-list {
        border-bottom: 1px solid #e5e5e5;
        white-space: nowrap;
        overflow-x: auto;
        .video-source-item {
          padding: 15px 10px 17px;
          margin-right: 47px;
          line-height: 40px;
          font-weight: 900;
          display: inline-block;
          box-sizing: border-box;
          min-width: 120px;
          border-bottom: 4px solid #fff;
          &.active {
            border-color:#F4A82B;
            color: #F4A82B;
          }
        }
      }
    }
    .play-resource {
      padding-top: 30px;
      white-space: nowrap;
      width: 100%;
      overflow: auto;
      .play-item {
        height: 120px;
        min-width: 120px;
        margin-right: 20px;
        box-sizing: border-box;
        padding: 0 20px;
        opacity: 0.8;
        background: #F5F5F5;
        border-radius: 17px;
        font-size: 36px;
        display: inline-block;
        text-align: center;
        line-height: 120px;
        padding: 0 20px;
        &.active {
          font-size: 36px;
          color: #F4A82B;
        }
      }
    }
  }
  .recomend {
    .title {
      padding: 0 30px;
      font-size: 32px;
      line-height: 45px;
      font-weight: 900;
      margin: 60px 0 20px;
    }
  }
  .screen_behind {
    .title {
      .right {
        font-size: 28px;
        font-weight: normal;
        color: #666;
        .arrowEl {
          border-color: #666;
        }
      }
    }
  }
}
</style>



