<template>
  <div class='intro-detail' style="z-index: 2" @click="closeFn">
    <transition name='drop'>
      <div class="intro-content" v-if="showDetail" @click.stop="''">
        <div class="close" @click="closeFn"></div>
        <div class="scroll-content scrollTouch">
          <div class="top-info flex-row">
            <div class="left">
              <div class="title">{{$utils.splitStr(details.vod_name, 15)}}</div>
              <div class="intro-tip">{{details.vod_en}}</div>
              <div class="intro-tip">{{details.vod_area}} | {{details.vod_class}} | {{details.vod_hits ? details.vod_hits : 0 | setPeople}}</div>
              <div class="intro-tip">{{details.vod_year}}</div>
              <div class="intro-tip">{{details.vod_director}}</div>
              <div class="intro-tip">{{details.vod_actor}}</div>
            </div>
            <div class="right">
              <div class="rect">
                <div class="text">: {{details.vod_douban_score ? details.vod_douban_score : ''}} <span v-if="!details.vod_douban_score" class="color666"></span></div>
              </div>
              <div class="cover">
                <img :src="details.vod_pic" onerror="this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/default_image.png'" :alt="details.vod_name">
              </div>
            </div>
          </div>
          <div class="content">
            <div class="title"></div>
            <div class="c-content" v-if="details.vod_content">{{details.vod_content}}</div>
            <div class="no-result" v-else>
              <div class="iconSize">
                <img src="../../../assets/img/movie/no-detail.png" alt="">
              </div>
              <div class="text"></div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  props: {
    details: {
      type: Object
    }
  },
  data () {
    return {
      showDetail: false
    }
  },
  methods: {
    closeFn () {
      this.showDetail = false
      setTimeout(() => {
        this.$emit('closeDetail')
      }, 400)
    }
  },
  mounted () {
    this.showDetail = true
  }
}
</script>
<style lang="scss" scoped>
.intro-detail {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba($color: #000000, $alpha: 0.75);
  .intro-content {
    height: 75%;
    background: #fff;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 100px;
    border-radius: 35px 35px 0 0;
    .close {
      position: absolute;
      right: 32px;
      top: 0;
      width: 80px;
      height: 80px;
      background: url('../../../assets/img/task/close_gray.png') no-repeat center;
      background-size: 30px 30px;
    }
    .scroll-content {
      height: 100%;
      padding: 0 35px 0px 38px;
      box-sizing: border-box;
      overflow: auto;
      .title {
        font-size: 36px;
        color: #333333;
        font-weight: 900;
        line-height: 50px;
        margin-bottom: 30px;
      }
      .top-info {
        align-items: flex-start;
        text-align: left;
        margin-bottom: 44px;
        .left {
          margin-right: 55px;
          text-align: justify;
          flex: 1;
        }
        .intro-tip {
          font-size: 28px;
          color: #999999;
          margin-bottom: 20px;
          word-break: break-all;
          line-height: 40px;
          max-height: 76px;
          overflow: hidden;
          text-overflow: ellipsis;
          text-overflow: -o-ellipsis-lastline;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          /* autoprefixer: off */
          -webkit-box-orient: vertical;
          /* autoprefixer: on */
        }
        .right {
          display: flex;
          flex-flow: column wrap;
          align-items: center;
          color: #999999;
        }
        .cover {
          width: 230px;
          height: 320px;
          position: relative;
          line-height: 0;
          background: #dadada;
          overflow: hidden;
          img {
            width: 100%;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
          }
        }
        .rect {
          color: #0DB918;
          display: flex;
          flex-flow: column wrap;
          padding: 14px 0 40px ;
          font-size: 28px;
          line-height: 40px;
          box-sizing: border-box;
          align-items: center;
          justify-content: space-between;
        }
      }
      .content {
        .c-content {
          font-size: 28px;
          line-height: 45px;
          text-align: justify;
          padding-bottom: 50px;
          color: #666;
        }
        .iconSize {
          width: 138px;
          height: 159px;
          margin-bottom: 20px;
        }
        .no-result {
          margin-top: 50px;
          display: flex;
          flex-flow: column wrap;
          font-size: 32px;
          line-height: 45px;
          color: #666;
          align-items: center;
        }
      }
    }
  }
}
</style>



