<template>
  <div class='intro-detail' style="z-index: 2" @click="closeFn">
    <transition name='drop'>
      <div class="intro-content" v-if="showDetail" @click.stop="''">
        <div class="close" @click="closeFn"></div>
        <div class="source" ref="source1" v-if="source.length >= 1">
          <div class="source-item" v-if="item[0]" v-for="(item, index) in details.playSource" :ref="index === activeSource ? 'resource1Child' + activeSource : ''" @click="setResource(index)" :class="{active: activeSource === index}" :key="index">{{'' + (index + 1)}}</div>
          <!-- <div class="source-item" v-if="item[0]" v-for="(item, index) in details.playSource" :ref="index === activeSource ? 'resource1Child' + activeSource : ''" @click="setResource(index)" :class="{active: activeSource === index}" :key="index">{{item[0] && item[0].source ? item[0].source : '' + (index + 1)}}</div> -->
        </div>
        <div class="watch-tip"></div>
        <div class="source" v-if="details.playSource[activeSource].length === 1">
          <span class="source-title" v-if="details.video_type === 1"></span>
          <span class="source-title" v-else-if="details.video_type === 2"></span>
          <span class="source-title" v-else-if="details.video_type === 3"></span>
          <span class="source-title" v-else-if="details.video_type === 4"></span>
        </div>
        <div class="classList" v-if="details.playSource[activeSource].length > 50">
          <div class="item" v-for="(item, index) in Math.ceil(details.playSource[activeSource].length / 50)" @click="setResource1(index)" :class="{active: activeIndex1 === index}" :key="index">
            <span v-if="index < Math.floor(details.playSource[activeSource].length / 50)">{{50 * index + 1}}-{{50 * (index + 1)}}</span>
            <span v-else>{{50 * index + 1}}-{{details.playSource[activeSource].length}}</span>
          </div>
        </div>
        <div class="scroll-content scrollTouch" :class="{scrollEL: details.playSource[activeSource].length <= 50}">
          <div class="class_detail">
            <div class="item" v-show="item.url" v-for="(item, index2) in details.playSource[activeSource].slice(activeIndex1 * 50, (activeIndex1 + 1) * 50)" @click="setResource2(index2)" :class="{active: activeIndex2 === index2}" :key="index2">{{item.title}}</div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  props: {
    details: {
      type: Object
    },
    type: {
      type: Number,
      default: 1
    },
    activeSource: {
      type: Number
    },
    faActiveIndex2: {
      type: Number
    }
  },
  data () {
    return {
      showDetail: false,
      tempActiveSource: 0,
      activeIndex: 0,
      activeIndex1: 0,
      activeIndex2: 0,
      soureceArrLength: 365,
      tempActive2: 0,
      soureceArr: [],
      source: [{

      }, {

      }, {

      }, {

      }]
    }
  },
  methods: {
    closeFn () {
      if (this.$refs.source1) {
        this.$utils.cache('source1Left', this.$refs.source1.scrollLeft)
      }
      this.showDetail = false
      setTimeout(() => {
        this.$emit('closeDetail')
      }, 400)
    },
    setResource (index) {
      this.activeIndex = index
      this.activeIndex1 = 0
      if (this.tempActiveSource === index) {
        this.activeIndex2 = this.tempActive2
      } else {
        this.activeIndex2 = ''
      }
      this.$utils.cache('source1Left', this.$refs.source1.scrollLeft)
      this.$emit('switchResource', {
        activeIndex: index
      })
    },
    setResource1 (index) {
      this.activeIndex1 = index
      this.activeIndex2 = ''
    },
    setResource2 (index) {
      this.activeIndex2 = index
      this.$emit('switchResource', {
        activeIndex: this.activeIndex,
        activeIndex1: this.activeIndex1,
        activeIndex2: this.activeIndex2
      })
    }
  },
  beforeRouteLeave (to, from, next) {
    // this.$utils.cache('source1Left', this.$refs.source1.scrollLeft)
    next()
  },
  mounted () {
    this.showDetail = true
    this.activeIndex = this.activeSource
    this.$nextTick(() => {
      if (this.$refs.source1) {
        this.$refs.source1.scrollLeft = this.$utils.cache('source1Left')
      }
    })
    this.tempActiveSource = this.activeSource
    this.tempActive2 = this.faActiveIndex2
    this.activeIndex2 = this.faActiveIndex2
  }
}
</script>
<style lang="scss" scoped>
.intro-detail {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba($color: #000000, $alpha: 0.75);
  .intro-content {
    height: 57%;
    background: #fff;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 35px 35px 0 0;
    .close {
      position: absolute;
      right: 32px;
      top: 0;
      width: 80px;
      height: 80px;
      background: url('../../../assets/img/task/close_gray.png') no-repeat center;
      background-size: 30px 30px;
    }
    .source {
      display: flex;
      margin-right: 140px;
      margin-left: 38px;
      white-space: nowrap;
      overflow-x: auto;
      border-bottom: 1px solid #f2f2f2;
      .source-item {
        margin-right: 50px;
        font-weight: 900;
        border-bottom: 5px;
        box-sizing: border-box;
        padding: 40px 0 16px;
        height: 100px;

        box-sizing: border-box;
        border-bottom: 5px solid #fff;
        font-size: 32px;
        line-height: 45px;
        &.active {
          border-color: #F4A82B;
          color: #F4A82B;
        }
      }
    }
    .scroll-content {
      margin-top: 30px;
      height: calc(100% - 318px);
      overflow: auto;
      &.scrollEL {
        height: calc(100% - 178px);
      }
      .class_detail {
        padding: 0px 0px 30px 35px;
        padding-right: 0;
        .item {
          height: 120px;
          min-width: 120px;
          box-sizing: border-box;
          white-space: nowrap;
          margin-right: 20px;
          text-align: center;
          display: inline-block;
          padding: 0 14px;
          line-height: 120px;
          margin-bottom: 50px;
          opacity: 0.8;
          background: #F5F5F5;
          border-radius: 17px;
          &:nth-child(5n) {
            // margin-right: 0;
          }
          &.active {
            color: #F4A82B;
          }
        }
      }
      
    }
  }
  .watch-tip {
    padding: 20px 30px 10px;
    line-height: 1.2;
    color: #999;
    border-bottom: 0;
  }
  .classList {
    padding: 0 30px;
    width: 100%;
    margin-top: 20px;
    white-space: nowrap;
    box-sizing: border-box;
    overflow: auto;
    .item {
      width: 182px;
      display: inline-block;
      text-align: center;
      min-width: 182px;
      margin-right: 30px;
      line-height: 108px;
      height: 108px;
      opacity: 0.8;
      background: #F5F5F5;
      font-weight: 36px;
      border-radius: 17px;
      &.active {
        color: #F4A82B;
      }
    }
  }
}
</style>



