<template>
  <div id='copyLink' style="z-index: 3">
    <div class="content">
      <div class="title"></div>
      <div class="copy-body">
        <div class="text">{{$store.state.common.userInfo.nickname ? $store.state.common.userInfo.nickname : ''}}{{details.vod_name}}<span class="redColor"></span></div>
        <div class="link">{{shortLink}}</div>
      </div>
      <div class="button clipbtn6" :data-clipboard-text="copyLink" @click="copyEvent()"></div>
      <div class="close" @click="closeFn"></div>
    </div>
  </div>
</template>
<script>
import Clipboard from 'clipboard'
export default {
  data () {
    return {

    }
  },
  props: {
    copyLink: {
      type: String
    },
    shortLink: {
      type: String
    },
    details: {
      type: Object
    }
  },
  methods: {
    copyEvent () {
      let that = this
      if (Clipboard.isSupported()) {
        let clb = new Clipboard('.clipbtn6')
        clb.on('success',  (e) => {
          that.$toast({
            message: '',
            duration: 1500
          })
          that.$emit('closeLink')
          clb.destroy()
        })
        clb.on('error',  (e) => {
          this.$toast({
            message: '',
            duration: 1500
          })
          clb.destroy()
        })
      } else {
        this.$toast({
          message: '',
          duration: 1500
        })
      }
    },
    closeFn () {
      this.$emit('closeLink')
    }
  },
  mounted () {
    
  }
}
</script>
<style lang="scss" scoped>
#copyLink {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba($color: #000000, $alpha: .75);
  .content {
    background: #FFFFFF;
    border: 1px solid #979797;
    border-radius: 20px;
    position: absolute;
    width: 697px;
    padding: 0 50px 28px;
    box-sizing: border-box;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    margin-top: -60px;
    .title {
      padding: 41px 0 25px;
      text-align: center;
      font-size: 32px;
      color: #333333;
      line-height: 45px;
      font-weight: 900;
    }
    .copy-body {
      background: #FFFFFF;
      box-shadow: 0 2px 11px 0 rgba(245,166,35,0.31), 0 -2px 11px 0 rgba(245,166,35,0.31);
      padding: 30px;
      border-radius: 34px;
      line-height: 38px;
      .text {
        font-size: 26px;
        margin-bottom: 20px;
        .redColor {
          color: #FF7F18;
          padding-left: 10px;
        }
      }
      .link {
        color: #FF7F18;
        user-select: text;
        height: 40px;
        line-height: 40px;
      }
    }
    .button {
      margin: 28px auto 0;
      width: 240px;
      height: 64px;
      background: #FF7F18;
      border-radius: 32px;
      color: #fff;
      box-sizing: border-box;
      text-align: center;
      line-height: 40px;
      font-size: 32px;
      padding: 12px 8px;
    }
  }
  .close {
    width: 58px;
    height: 58px;
    position: absolute;
    left: 50%;
    bottom: -100px;
    transform: translateX(-50%);
    background-image: url('../../../assets/img/task/close.png');
    background-size: 45px 45px;
    background-repeat: no-repeat;
  }
}
</style>



