 60px;\n          height: 60px;\n          border-radius: 50%;\n          overflow: hidden;\n          margin-right: 20px;\n        }\n      }\n      .right {\n        flex:1;\n        .userInfo {\n          align-items: flex-end;\n          margin-bottom: 5px;\n          line-height: 1.2;\n          .nickname {\n            text-align: left;\n            margin-right: 20px;\n            color: #666;\n            &.is_self {\n              color: #FF4112;\n            }\n          }\n          .grade {\n            flex: 1;\n          }\n          .digg_info {\n            display: flex;\n            align-items: flex-end;\n            .digg-icon {\n              width: 39px;\n              height: 42px;\n              margin-right: 8px;\n            }\n            .text {\n              line-height: 34px;\n            }\n            &.active {\n              .text {\n                color: #ff4b1f\n              }\n            }\n          }\n        }\n        .content {\n          line-height: 42px;\n          max-height: 126px;\n          text-align: justify;\n          font-size: 30px;\n          overflow: hidden;\n          text-overflow: -o-ellipsis-lastline;\n          text-overflow: ellipsis;\n          display: -webkit-box;\n          -webkit-line-clamp: 3;\n          line-clamp: 3;\n          /* autoprefixer: off */\n          -webkit-box-orient: vertical;\n          /* autoprefixer: on */\n        }\n        .others {\n          text-align: left;\n          display: flex;\n          justify-content: flex-start;\n          align-items: center;\n          margin-top: 5px;\n          .update_time {\n            color: #666;\n            line-height: 38px;\n            margin-bottom: 20px;\n          }\n          .edit, .delete {\n            border: 1px solid #F4A82B;\n            border-radius: 5px;\n            width: 74px;\n            height: 38px;\n            line-height: 38px;\n            text-align: center;\n            color: #F4A82B;\n            margin-left: 20px;\n          }\n          .delete {\n            color: #ff4b1f;\n            border-color: #ff4b1f;\n          }\n        }\n      }\n    }\n    .edit-content {\n      padding: 0 20px;\n      margin-top: 10px;\n      .generate {\n        margin: 0;\n        width: 238px;\n      }\n      .edit, .delete {\n        color: #F4A82B;\n        font-size: 28px;\n        border: 1px solid #D9B056;\n        line-height: 40px;\n        border-radius: 32.5px;\n        padding: 15px 36px 12px;\n      }\n    }\n    .button {\n      margin: 0 auto;\n      border-radius: 6px;\n      width: 323px;\n      background: #F4A82B;\n      border: 1px solid #D9B056;\n      border-radius: 32.5px;\n      height: 65px;\n      color: #fff;\n      &.no_review {\n        margin-top: 30px;\n      }\n    }\n  }\n  .check-all {\n    text-align: center;\n    color: #999;\n    padding: 32px;\n  }\n  .commentEdit {\n    position: fixed;\n    left: 0;\n    right: 0;\n    top: 0;\n    bottom: 0;\n    background: rgba($color: #000000, $alpha: .6);\n    .commentEditBox {\n      position: absolute;\n      top: 160px;\n      left: 20px;\n      right: 20px;\n      background: #fff;\n      border-radius: 30px;\n      .close {\n        width: 60px;\n        height: 60px;\n        background: url('../../../assets/img/task/close_circle.png') no-repeat center;\n        background-size: 46px 46px;\n        position: absolute;\n        bottom: -100px;\n        left: 50%;\n        transform: translateX(-50%);\n        -webkit-transform: translateX(-50%);\n      }\n      .gradeCount {\n        line-height: 50px;\n        text-align: center;\n        font-size: 50px;\n        color: #f4a82b;\n        .percennt {\n          font-size: 30px;\n        }\n      }\n      .scroe {\n        text-align: center;\n        line-height: 50px;\n        // color: #f4a82b;\n        font-size: 36px;\n      }\n      .edit-title {\n        max-width: 80%;\n        text-align: center;\n        margin: 0 auto;\n        padding: 40px 0 45px;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        white-space: nowrap;\n        font-weight: 900;\n        font-size: 36px;\n        line-height: 50px;\n      }\n      .grade {\n        height: 70px;\n        .grade-icon {\n          height: 72px;\n          background: url('../../../assets/img/movie/grade.png') no-repeat left top;\n          width: 370px;\n          background-size: 370px;\n          .grade_progress {\n            height: 70px;\n            background: url('../../../assets/img/movie/grade.png') no-repeat bottom left;\n            background-size: 370px;\n          }\n        }\n        .acturally {\n          font-size: 36px;\n          width: 40px;\n        }\n      }\n      .tip {\n        font-size: 32px;\n        color: #666;\n        margin-bottom: 45px;\n        text-align: center;\n      }\n      .grade {\n        justify-content: center;\n      }\n      .edit-input {\n        margin-top: 40px;\n        border-bottom: 1px solid #e5e5e5;\n        border-top: 1px solid #e5e5e5;\n        padding: 30px 30px 16px;\n        color: #666;\n        height: 350px;\n        box-sizing: border-box;\n        textarea {\n          width: 100%;\n          height: calc(100% - 56px);\n          display: block;\n          font-size: 32px;\n          line-height: 1.5;\n          border: 0;\n        }\n        .text-right {\n          line-height: 40px\n        }\n      }\n      .edit-publish {\n        padding: 42px 0 38px;\n        font-size: 36px;\n        line-height: 50px;\n        box-sizing: border-box;\n        text-align: center;\n        border-top: 1px solid #f5f5f5;\n        margin: 0 auto;\n        &.active {\n          color: #F4A82B;\n        }\n      }\n    }\n  }\n}\n",null],"sourceRoot":""}]);

