n\n\n\n\n\n\n\n\n\n\n\n\n\n\n#film {\n  padding-top: 420px;\n  font-size: 28px;\n  overflow: auto;\n  position: relative;\n  min-height: 100vh;\n  &.isIos{\n    padding-top: 508px;\n  }\n  &.Router {\n    transition: all 0s ease;\n  }\n  .arrowEl {\n    width: 16px;\n    height: 14px;\n    margin-bottom: 2px;\n    border-color: rgba($color: #6BA5D8, $alpha: .76);\n  }\n  .film_video {\n    position: fixed;\n    left: 0;\n    right: 0;\n    top: 0;\n    background: #000;\n    &.isIos {\n      position: absolute;\n      top: 88px;\n    }\n    height: 420px;\n    z-index: 1;\n    .noLogin {\n      position: absolute;\n      left: 0;\n      top: 0;\n      width: 100%;\n      height: 100%;\n      box-sizing: border-box;\n      .posC {\n        top: 136px;\n        color: #fff;\n        line-height: 45px;\n        text-align: center;\n        .login {\n          margin-top: 36px;\n          padding: 12px 0 10px;\n          width: 240px;\n          border-radius: 10px;\n          background: #FF7F18;\n        }\n      }\n    }\n    .noplayUrl {\n      position: absolute;\n      left: 0;\n      right: 0;\n      top: 0;\n      bottom: 0;\n      z-index: 1;\n      //  background: url('../../../assets/img/movie/video.png') no-repeat left  70px;\n      background: #000;\n      background-size: 100%;\n      text-align: center;\n      &::after{\n        content: '';\n        position: absolute;\n        left: 0;\n        right: 0;\n        top: 0;\n        bottom: 0;\n        background-image: linear-gradient(-180deg, rgba(53,53,53,0.00) 38%, rgba(54,54,54,0.87) 96%);\n        background-size: 100% 125px;\n        background-position: 0 295px;\n        background-repeat: no-repeat;\n        z-index: 1;\n      }\n      .no-title {\n        position: absolute;\n        top: 125px;\n        left: 50%;\n        transform: translateX(-50%);\n        line-height: 1.5;\n        -webkit-transform: translateX(-50%);\n        font-size: 30px;\n        line-height: 45px;\n        color: #fff;\n        z-index: 2;\n      }\n      .text {\n        margin-bottom: 8px;\n        font-size: 32px;\n      }\n      .sec-text {\n        white-space: nowrap;\n        margin-bottom: 18px;\n        font-size: 28px;\n      }\n      .iconSize {\n        width: 100px;\n        height: 100px;\n        margin: 0 auto;\n        img {\n          display: block;\n        }\n      }\n    }\n    .normal {\n      height: 100%;\n      overflow: hidden;\n      background: url('../../../assets/img/movie/loading-bg.gif') no-repeat center;\n      background-size: 80px 80px;\n      z-index: 0;\n      .wrapper {\n        position: relative;\n        height: 100%;\n        overflow: hidden;\n      }\n    }\n    .video-content {\n      display: block;\n      width: 100%;\n      height: 100%;\n      .video {\n        display: block;\n        width: 100%;\n        height: 100%;\n      }\n    }\n    .watch-history {\n      position: absolute;\n      left: 0;\n      bottom: 92px;\n      z-index: 3;\n      color: #fff;\n      font-size: 28px;\n      background: rgba($color: #000000, $alpha: .25);\n      height: 70px;\n      border-radius: 0 35px 35px 0;\n      .close {\n        width: 25px;\n        height: 25px;\n        padding: 25px;\n      }\n      .jumpPlay {\n        padding: 0 25px;\n        color: #FF7F18;\n      }\n    }\n  }\n  //  \n  .play-source {\n    width: 706px;\n    margin: 30px auto;\n    background: #F5F5F5;\n    border-radius: 8px;\n    .title {\n      font-size: 26px;\n      color: #666666;\n      padding: 30px 24px;\n    }\n    .play-source-list {\n      padding: 10px;\n      box-sizing: border-box;\n      &.isFlex {\n        display: flex;\n        justify-content: space-around;\n      }\n    }\n    .item {\n      min-width: 120px;\n      height: 80px;\n      box-sizing: border-box;\n      padding: 0 10px;\n      text-align: center;\n      margin: 8px;\n      display: inline-block;\n      line-height: 80px;\n      background: rgba($color: #D1BD9B, $alpha: .3);\n   