<template>
  <div id="shareBox" class="maskBox" :class="{noQQ: showTreasureBox}" @click="shareAction('cancel')" style="z-index: 15">
    <div class="shareContent" @click.stop="''" :class="{active: active, isWx: $utils.isWx(), muti: $config.source_style >= 6 || $utils.isWx()}">
      <div class="shareTitle"></div>
      <div class="shareWay" v-if="$utils.isWx() || $config.source_style >= 6">
        <!--  -->
        <div class="shareFlex flex-row" v-if="$config.source_style >= 6 || $utils.isWx()" @click="shareAction('weixin')">
          <div class="iconSize">
            <img src="../../../assets/img/my/share_wechat.png" alt="">
          </div>
          <div class="text"></div>
        </div>
        <!--  -->
        <div class="shareFlex flex-row" v-if="$config.source_style >= 6 || $utils.isWx()" @click="shareAction('friend')">
          <div class="iconSize">
            <img src="../../../assets/img/my/share_friend.png" alt="">
          </div>
          <div class="text"></div>
        </div>
        <!-- QQ -->
        <div class="shareFlex flex-row" v-if="$config.source_style >= 6 || $utils.isWx()" @click="shareAction('qq')">
          <div class="iconSize">
            <img src="../../../assets/img/my/share_qq.png" alt="qq">
          </div>
          <div class="text">QQ</div>
        </div>
      </div>
      <div class="shareWay sec">
        <!--  -->
        <div class="shareFlex flex-row"  @click="shareAction('link')">
          <div class="iconSize">
            <img src="../../../assets/img/my/share_link.png" alt="">
          </div>
          <div class="text">{{$config.source_style >= 6 ? '' : ''}}</div>
        </div>
        <!--  -->
        <div class="shareFlex flex-row" @click="shareAction('image')">
          <div class="iconSize">
            <img src="../../../assets/img/my/share_icon.png" alt="">
          </div>
          <div class="text"></div>
        </div>
      </div>
      <div class="cancel flex-row" @click="shareAction('cancel')"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    showTreasureBox: {
      type: Boolean,
      default: false
    },
    id: {
      type: Number
    }
  },
  data () {
    return {
      active: false
    }
  },
  methods: {
    shareAction (action) {
      this.$emit('shareAction', action)
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.active = true
    })
  }
}
</script>
<style lang="scss" scoped>
#shareBox {
  @keyframes moveTo {
    from {
      bottom: -500px;
    }
    to {
      bottom: 24px;
    }
  }
  .shareContent {
    position: absolute;
    width: 697px;
    bottom: 24px;
    height: 0;
    transform: translate3d(-50%, 0, 0);
    overflow: hidden;
    top: auto;
    animation: moveTo 300ms ease;
    .shareTitle {
      font-size: 32px;
      line-height: 45px;
      text-align: center;
      padding: 27px 0 0;
    }
    .shareWay {
      display: flex;
      justify-content: space-between;
      padding: 0px 60px;
      margin-top: 32px;
      margin-bottom: 32px;
      .shareFlex {
        flex: 1;
        display: flex;
        flex-flow: column;
        justify-content: center;
        .iconSize {
          width: 100px;
          height: 100px;
        }
        .text {
          color: #232323;
          line-height: 40px;
          font-size: 28px;
          padding-top: 10px;
        }
      }
    }
    .cancel {
      border-top: 1px solid #dadada;
      font-size: 32px;
      color: #333333;
      height: 84px;
      line-height: 84px;
      text-align: center;
      bottom: 0;
    }
    &.isWx {
      .shareWay {
        .shareFlex {
          .iconSize {
            width: 80px;
            height: 80px;
          }
        }
      }
    }
  }
  .shareContent.active {
    height: 370px;
  }
  .shareContent.active.muti {
    height: 507px;
    .shareWay {
      margin-bottom: 0;
      .shareFlex {
        .iconSize {
          width: 80px;
          height: 80px;
        }
      }
      &.sec {
        padding: 0 130px 30px;
      }
    }
  }
  &.noQQ {
    .shareFlex:nth-child(3){
      display: none;
    }
  }
}
</style>



