<template>
  <div id="confirmBottom" style="z-index: 5">
    <div class="content">
      <div class="title">{{config.title}}</div>
      <div class="buttons">
        <div class="cancel" @click.stop="cancelFn">{{config.cancel ? config.cancel : ''}}</div>
        <div class="confirm" @click.stop="confirmFn">{{config.confirm ? config.confirm : ''}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    config: {
      type: Object
    }
  },
  methods: {
    cancelFn () {
      this.$emit('setFn', {actions: 'cancel'})
    },
    confirmFn () {
      this.$emit('setFn', {actions: 'confirm'})
    }
  }
}
</script>

<style lang="scss" scoped>
#confirmBottom {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba($color: #000000, $alpha: .75);
  .content {
    position: absolute;
    bottom: 370px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    width: 520px;
    border-radius: 27px;
    background: #fff;
    .title {
      padding: 42px 70px 30px;
      line-height: 45px;
      text-align: center;
      border-bottom: 1px solid #e5e5e5;
    }
    .buttons {
      display: flex;
      border-top: #EBEAEF;
      > div {
        width: 50%;
        box-sizing: border-box;
        flex: 1;
        text-align: center;
        padding: 30px 0 26px;
        line-height: 45px;
      }
      .cancel {
        border-right: 1px solid #EBEAEF;
      }
      .confirm {
        color: #F4A82B;
      }
    }
  }
}
</style>



