<template>
  <div id='allComment' class="normalMask">
    <div class="allComment">
      <div class="title">
        <span>{{total}}</span>
        <div class="close" @click="close"></div>
      </div>
      <div class="scrollTouch">
        <div class="loadmore" infinite-scroll-distance="200" v-infinite-scroll="loadMore">
          <film-list :list='reviewList' @filmOperate="filmOperate"></film-list>
          <list-loading v-if="loading === 1" :loading="loading" :page="2"></list-loading>
          <div class="no-list-data" v-if="loading === 2 && reviewList.length > 0">{{loading === 1 ? '...' : '!'}}</div>
          <div class="no-comment" v-if="loading === 2 && reviewList.length === 0">
            <div class="no-list-data"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import FilmList from './film-comment-list'
import ListLoading from './list-loading'
// import 
export default {
  components: {
    FilmList,
    ListLoading
  },
  props: {
    ChangeObj: {
      type: Object
    }
  },
  data () {
    return {
      page: 1,
      loading: 0,
      reviewList: [],
      total: 0,
      isRequest: false
    }
  },
  methods: {
    getList (cb) {
      let data = {
        page: this.page,
        vod_id: this.$route.params.id,
        per_page: 12
      }
      this.$api.get('MOVIE_REVIEW_LIST', data, res => {
        cb && cb(res)
        this.loading = 0
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
        this.total = res.data.total
      }, res => {
        this.loading = 0
      })
    },
    loadMore () {
      if (this.loading === 1) {
        setTimeout(() => {
          this.loading = 0
        }, 1000)
        return
      } else if (this.loading === 2) {
        return
      }
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.loading = 1
      this.timer = setTimeout(() => {
        this.getList(res => {
          this.reviewList = [...this.reviewList, ...res.data.data]
        })
      }, 300)
    },
    close () {
      this.$emit('close')
    },
    filmOperate ({index, operate}) {
      if (operate === 1) {
        this.$emit('filmOperate', {index, operate})
      } else if (operate === 2) {
        let data = {
          vod_id: this.$route.params.id,
          reviewid: this.reviewList[index].reviewid
        }
        this.$api.post('DELETE_MOVIE_COMMENT', data, res => {
          // 
          this.reviewList.splice(index, 1)
          this.total--
          this.$emit('changeDelete', index)
        })
        // 
      } else if (operate === 3) {
        let obj = this.reviewList[index]
        this.$api.post('MOVIE_COMMENT_FABULOUS', {vod_id: this.$route.params.id, reviewid: obj.reviewid}, res => {
          obj.fabulous++
          obj.is_fabulous = 1
        })
      }
    }
  },
  mounted () {
    this.loading = 1
    this.getList(res => {
      this.isRequest = true
      this.reviewList = res.data.data
    })
  },
  watch: {
    'ChangeObj.count' (val) {
      if (this.ChangeObj.type === 'change') {
        this.$set(this.reviewList, 0, this.ChangeObj.obj)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#allComment {
  .allComment {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 75%;
    background: #fff;
    border-radius: 35px 35px 0 0;
    .title {
      line-height: 45px;
      font-size: 32px;
      font-weight: 900;
      padding: 35px 30px 20px;
      position: relative;
      .close {
        width: 80px;
        height: 80px;
        background: url('../../../assets/img/task/close_gray.png') no-repeat center;
        position: absolute;
        right: 20px;
        top: 10px;
        background-size: 30px 30px;
      }
    }
    .scrollTouch {
      height: calc(100% - 100px);
      overflow: auto;
    }
    .no-comment {
      margin-top: 300px;
    }
  }
}

</style>



